:root {
  --primary-color: #adb5bd;
  --secondary-color: #343a40;
  --tertiary-color: #f8f9fa;
  --text-color: #65def1;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
 background-image: url('./assets/photos/portfolio.jpg');
 background-repeat: no-repeat;
 background-attachment: fixed;
 background-position: center left;
}

a {
  text-decoration: none;
  color: var(--primary-color);
  font-weight: bolder;
}

h1 {
  font-size: 50px;
  margin: 15px 15px 15px 50px;
}

header, footer {
  background: var(--secondary-color);
  opacity: .8;
}

header {
  /* position: fixed; */
  width: 100%;
  color: var(--primary-color);
}

footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  font-size: 20px;
}

.nav-item {
  font-size: 25px;
}

.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-grow: 2;
}

.nav-body {
  align-items: center;
}

.footer-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-grow: 2;
}

.footer-items {
  font-size: 40px;
  padding: 10px;
}

.about-container {
  background-color: var(--tertiary-color);
  margin: 40px 40px 40px 40px ;
  opacity: .8;
  padding: 10px;
  border-style: double;
  border-radius: 25px;
  border-color: var(--secondary-color);
}

.about-icon {
  margin: 20px;
  font-size: 40px;
}

.about-text {
  margin: 20px;
  font-size: 25px;
}

img {
  border-radius: 12px;
  height: 214px;
  object-fit: cover;
}

.single-project-container {
  display: flex;
  flex-direction: row;
  max-width: 300px;

  /* Center the container in middle on horizontal axis */
  margin: 0 auto;

  /* Add empty space above the container (20% of the view height) */
  margin-top: 5vh;
}

.single-project-card {
  /* Change background color */
  background-color: var(--secondary-color);

  /* Add border */
  border: 1px solid #bacdd8;

  /* Add space between the border and the content */
  padding: 8px;

  border-radius: 12px;
}

.single-project-caption {
  margin-left: 10px;
  font-size: 20px;
  color: var(--text-color);
  line-height: 150%;
}

.single-project-link {
  border: none;
  padding: 12px 24px;
  border-radius: 50px;
  
  text-align: center;
  font-weight: 700;
  color: black;
  background-color: #e0efff;

  /* Button is inline-block element by default, it need to have block display for margin: 0 auto; to work */
  margin: 0 auto;
  display: block;

  /* Button is a clickable element, therefore it should have a pointer cursor */
  cursor: pointer;
}

.portfolio-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  flex-grow: 2;
  margin-bottom: 10vh;
}

.portfolio-title-container {
  width: 100%;
  background-color: var(--secondary-color);
  opacity: .7;
}

.portfolio-title {
  font-size: 50px;
  text-align: center;
  color: var(--text-color);
}

.portfolio-project-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 80%;
}

*:focus {
  outline: 0;
}

.contact-container {
  padding: 12px 24px 24px 24px;
  margin: 48px 12px;
  border-radius: 4px;
  background-color: var(--secondary-color);
  opacity: .9;
}

label {
  font-size: .85em;
  margin-left: 12px;
}

input[type=text], input[type=email], textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}

input[type=text]:focus, input[type=email]:focus, textarea:focus {
  border: 3px solid var(--text-color);
}

input[type=submit]:focus {
  background-color: var(--text-color);
}

input[type=submit]:hover {
  background-color: var(--text-color);
}

.submit-btn-container {
  text-align: center;
  border-radius: 4px;
}

.submit-btn {
  padding: 5px 15px 5px 15px;
  font-size: 15px;
}

.contact-title, .contact-form-text {
  color: var(--text-color);
}

.contact-title {
  font-size: 40px;
}

.contact-form-text {
  font-size: 25px;
}